.my-blog-title {
  text-decoration: none;
  color: white;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.my-blog-title:hover {
  color: #b1b1b1;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.login-button {
  margin-left: auto;
  text-decoration: none;
  color: white;
}

.logout-button {
  margin-left: auto;
  text-decoration: none;
  color: white;
}


